import React from 'react';
import Preloader from '../../common/Preloader/Preloader';
import s from './ProfileInfo.module.css';
import yes from '../../../assets/images/yes-job.png';
import no from '../../../assets/images/no-job.png';
import userPhoto from '../../../assets/images/user.png';
import ProfileStatus from './ProfileStatus';
import ProfileStatusWithHooks from './ProfileStatusWithHooks';

const ProfileInfo = (props) => {

    if (!props.profile) {
        return <Preloader />
    }

    let isContacts = (socialNetwork) => {
        return socialNetwork ? socialNetwork : undefined
    }

    return <div>
        <div>
            <img className={s.imgContent} src='https://helpx.adobe.com/content/dam/help/en/photoshop/using/convert-color-image-black-white/jcr_content/main-pars/before_and_after/image-before/Landscape-Color.jpg' />
        </div>
        <div className={s.descriptionBlock}>

            <div>
                <img className={s.userAvatar} src={props.profile.photos.large != null ? props.profile.photos.large : userPhoto} />
            </div>

            <div>

                <div className={s.fullName}>{props.profile.fullName}</div>
                <ProfileStatusWithHooks status={props.status} updateStatus={props.updateStatus} />
                <div>{props.profile.aboutMe}</div>
                <div className={s.lookingForJobContainer}>
                    Look for a job:
                    <img className={s.lookingForAJob} src={props.profile.lookingForAJob ? yes : no} />
                </div>
                <div>
                    <div className={s.lookingForAJobDescriptionContainer}>Description about job: </div>
                    <div className={s.lookingForAJobDescription}>{props.profile.lookingForAJobDescription}</div>
                </div>
                <div>My contacts:
                    <div>Facebook: {isContacts(props.profile.contacts.facebook)} </div>
                    <div>Website: {isContacts(props.profile.contacts.website)}</div>
                    <div>Vkontakte: {isContacts(props.profile.contacts.vk)}</div>
                    <div>Twitter: {isContacts(props.profile.contacts.twitter)}</div>
                    <div>Instagram: {isContacts(props.profile.contacts.instagram)}</div>
                    <div>Youtube: {isContacts(props.profile.contacts.youtube)}</div>
                    <div>Github: {isContacts(props.profile.contacts.github)}</div>
                    <div>Main Link: {isContacts(props.profile.contacts.mainLink)}</div>
                </div>
            </div>
            
        </div>
    </div>
}

export default ProfileInfo;