import React from 'react';
import s from './Post.module.css';

const Post = (props) => {
    return (
        <div className={s.item}>
            <img src='https://st2.depositphotos.com/44024866/43356/v/600/depositphotos_433569624-stock-illustration-personal-name-ava-vector-set.jpg'
                className={s.img} />
            {props.message}
            <div>
                <span>Like: {props.likeCounts}</span>
            </div>
        </div>
    )
}

export default Post;